<template lang='pug'>
.container
  .container-box
    el-form(ref="form" :model="form" label-width="80px")
      el-form-item(label="手机号码")
        el-input(v-model="form.phone")
      el-form-item(label="账号")
        el-input(v-model="form.account" )
      el-form-item(label="密码")
        el-input(v-model="form.password" type="password")
      el-form-item(label="角色")
        el-radio-group(v-model="form.role")
          el-radio(v-model="form.role" label="1") 学生
          el-radio(v-model="form.role" label="2") 老师
      el-form-item
        el-button(type="primary" @click="onSubmit") 立即注册
        el-button 取消
</template>
<script>
import common from '@/common';
export default {
name: 'Register',
 data() {
return {
    form: {
          phone:'',
          account:'',
          password:"",
          role:'',
        }
};
},
methods: {
  onSubmit(){
       let vm=this;
       let params = this.form;
       common.fetchData({
          method: 'get',
          url: CONSTANT.url.register,
          params: params,
          success(data) {
             console.log(data)
            // setTimeout(()=>{
            //     vm.$router.push({name:"Login"});
            // },2000)
          }
        });
  }
},
components: {}
}
</script>
<style scoped lang='stylus'>
  .container
    height 500px
    .container-box
      width 30%
      margin 50px auto 0
      border 1px solid #ccc
      padding 50px
</style>



